<template>
  <div class="sign-up-template col-lg-12"
       style="width: 30%;-webkit-box-shadow: 6px 9px 47px -5px rgba(100,17,63,1);
            -moz-box-shadow: 6px 9px 47px -5px rgba(100,17,63,1);
            box-shadow: 6px 9px 47px -5px rgba(100,17,63,1);">
    <div class="title">
      <img src="../assets/cloudlogo.png" alt="cloud" width="200">
      <h2 class="CompTitle" style="color: #64113F;">CyberLocksmith</h2>
    </div>
    <div class="form-div">
      <form>
        <h3 class="FormTitle">Forgot Password</h3>
        <div class="mb-3">
          <span style="float: left;color: grey">Email Address</span>
          <input type="email" class="form-control  form-control-lg" v-model="email"
                 @blur="validateEmail" required/>
          <p v-if="emailError" class="error">{{ emailError }}</p>
          <br>
          <button class="btn btn-secondary bg-burgundy btn-lg btn-block" onclick="">
            Reset Password
          </button>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
// eslint-disable-next-line import/no-extraneous-dependencies
import Swal from 'sweetalert2';

export default {
  data() {
    return {
      email: '',
      emailError: '',
    };
  },


  methods: {
    validateEmail() {
      const emailPattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|.(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      if (!this.email) {
        this.emailError = 'Email field cannot be empty.';
      } else if (!emailPattern.test(this.email)) {
        this.emailError = 'Please enter a valid email address.';
      } else {
        this.emailError = '';
      }
    },
    showSuccessAlert() {
      Swal.fire({
        icon: 'success',
        title: 'E-mail sent successfully',
        timer: 5000,
        timerProgressBar: true,
        showConfirmButton: false,
        toast: true,
        position: 'top-end',
        onOpen: (toast) => {
          toast.addEventListener('mouseenter', Swal.stopTimer);
          toast.addEventListener('mouseleave', Swal.resumeTimer);
        },
      });
    },
    showFailAlert() {
      Swal.fire({
        icon: 'error',
        title: 'error',
        timer: 5000,
        timerProgressBar: true,
        showConfirmButton: false,
        toast: true,
        position: 'top-end',
        onOpen: (toast) => {
          toast.addEventListener('mouseenter', Swal.stopTimer);
          toast.addEventListener('mouseleave', Swal.resumeTimer);
        },
      });
    },
  },
};

</script>

